.container{
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image:url("./images/background.jpg");
  background-size: cover;
  height:742px;
}
.content ul{
  display: flex;
  list-style: none;
  gap:1em;
}
.footer{
  position:absolute;
  bottom: 5em;
  font-size: large;
  color:white;
}

.forecast {
  width: 100%;
  height: 100%;
}

.forecast-icon {
  margin-left:4em;
  padding-top: 1em;
  /* background: white; */
}

.today-weather h3 {
  text-transform: capitalize;
  font-size:25px;
  color: #fff;
  width: 80%;
  margin: 0 auto 10px;
  padding-bottom: 10px;
  border-width: thin;
  border-bottom: 1px solid #eee;
}
.search-box input{
  width:7em;
  position: relative;
  left:5em
}
.img-box img{
  height: 1em;
  position: relative;
  top:-1rem;
  left:10.7em
}
.today-weather ul li {
  font-size: 1em;
  width: 100%;
  line-height: 39px;
  border-width: thin;
  border-bottom: 1px solid #777;
  color: #eee;
  font-weight: 600;
  text-align: left;
  padding-left: 20px;
}

.today-weather ul li span {
  float: right;
  padding-right: 10px;
}

.today-weather ul li:nth-child(5) {
  border-bottom: none;
}


